@import '~node_modules/modern-normalize/modern-normalize.css';
.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
      opacity 250ms cubic-bezier(0.4, 0, 0.2, 1) 200ms,
      visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }